"Selecione qual n\xEDvel renderizar pelo seu \xEDndice (LDtk)","Animation FPS":"FPS da anima\xE7\xE3o","Tilemap":"Tilemap","Displays a tiled-based map, made with the Tiled editor (https://www.mapeditor.org/) or the LDtk editor (https://ldtk.io/).":"Exibe um mapa baseado em tiles, criado com o editor Tiled (https://www.mapeditor.org/) ou o editor LDtk (https://ldtk.io/).","Check the tilemap file (Tiled or LDtk) being used.":"Verificar o arquivo do tilemap (Tiled ou LDtk) sendo usado.","The tilemap file of _PARAM0_ is _PARAM1_":"O arquivo de tilemap de _PARAM0_ \xE9 _PARAM1_","Tile map":"Mapa de Telhas","Set the Tiled or LDtk file containing the Tilemap data to display. This is usually the main file exported from Tiled/LDtk.":"Defina o arquivo Tiled ou LDtk que cont\xE9m os dados do Tilemap a ser exibido. Geralmente, esse \xE9 o arquivo principal exportado do Tiled/LDtk.","Set the tilemap file of _PARAM0_ to _PARAM1_":"Definir o arquivo de tilemap de _PARAM0_ a _PARAM1_","Tileset JSON file":"Arquivo JSON do Tileset","Check the tileset JSON file being used.":"Verifique o arquivo JSON do tileset em uso.","The tileset JSON file of _PARAM0_ is _PARAM1_":"O arquivo JSON do tileset de _PARAM0_ \xE9 _PARAM1_","Set the JSON file with the tileset data (sometimes that is embedded in the Tilemap, so not needed)":"Defina o arquivo JSON com os dados do tileset (\xE0s vezes est\xE1 incorporado no Tilemap, portanto n\xE3o \xE9 necess\xE1rio)","Set the tileset JSON file of _PARAM0_ to _PARAM1_":"Define o arquivo JSON do tileset de _PARAM0_ para _PARAM1_","Compare the value of the display mode.":"Compara o valor do modo de exibi\xE7\xE3o.","The display mode of _PARAM0_ is _PARAM1_":"O modo de exibi\xE7\xE3o de _PARAM0_ \xE9 _PARAM1_","Set the display mode":"Define o modo de exibi\xE7\xE3o","Set the display mode of _PARAM0_ to _PARAM1_":"Define o modo de exibi\xE7\xE3o de _PARAM0_ para _PARAM1_","Layer index":"\xCDndice da camada","Compare the value of the layer index.":"Compara o valor do \xEDndice da camada.","the layer index":"o \xEDndice de camada","Set the layer index of the Tilemap.":"Define o \xEDndice da camada do Tilemap.","Get the layer index being displayed":"Obt\xE9m o \xEDndice da camada sendo exibida","Level index":"\xCDndice de n\xEDvel","the level index being displayed.":"o \xEDndice de n\xEDvel sendo exibido.","the level index":"o \xEDndice de n\xEDvel","Compare the animation speed scale.":"Compara a escala de velocidade da anima\xE7\xE3o.","Speed scale to compare to (1 by default)":"Velocidade a ser comparada (1 por padr\xE3o)","Set the animation speed scale of the Tilemap.":"Definir a velocidade de anima\xE7\xE3o do tilemap.","Speed scale (1 by default)":"Velocidade (1 por padr\xE3o)","Get the Animation speed scale":"Obt\xE9m a escala de velocidade da anima\xE7\xE3o","Animation speed (FPS)":"Velocidade da anima\xE7\xE3o (FPS)","Compare the animation speed.":"Comparar a velocidade da anima\xE7\xE3o.","the animation speed (FPS)":"a velocidade da anima\xE7\xE3o (FPS)","Animation speed to compare to (in frames per second)":"Velocidade da anima\xE7\xE3o para compara\xE7\xE3o (em quadros por segundo)","Set the animation speed of the Tilemap.":"Definir a velocidade da anima\xE7\xE3o do tilemap.","Animation speed (in frames per second)":"Velocidade da anima\xE7\xE3o (em quadros por segundo)","Get the animation speed (in frames per second)":"Obt\xE9m a velocidade da anima\xE7\xE3o (em quadros por segundo)","Tilemap JSON file":"Arquivo JSON Tilemap","This is the JSON file that was saved or exported from Tiled. LDtk is not supported yet for collisions.":"Este \xE9 o arquivo JSON que foi salvo ou exportado do Tiled. Ainda n\xE3o h\xE1 suporte para colis\xF5es no LDtk.","Optional, don't specify it if you've not saved the tileset in a different file.":"Opcional, n\xE3o especifica se voc\xEA n\xE3o salvou o tileset em um arquivo diferente.","Class filter":"Filtro de classe","Only the tiles with the given class (set in Tiled 1.9+) will have hitboxes created.":"Apenas as telhas com a classe dada (definida em Tiled 1.9+) ter\xE3o caixas de sucesso criadas.","When activated, it displays the hitboxes in the given color.":"Quando ativado, ele exibe as caixas de hits na cor dada.","Tilemap collision mask":"M\xE1scara de colis\xE3o de mapas","Invisible object handling collisions with parts of a tilemap.":"Objeto invis\xEDvel que lida com colis\xF5es com partes de um ladrilho.","Check the Tilemap JSON file being used.":"Verifica o arquivo JSON do Tilemap em uso.","The Tilemap JSON file of _PARAM0_ is _PARAM1_":"O arquivo JSON do Tilemap de _PARAM0_ \xE9 _PARAM1_","Tile map collision mask":"M\xE1scara de colis\xE3o de mapas","Set the JSON file containing the Tilemap data to display. This is usually the JSON file exported from Tiled.":"Define o arquivo JSON que cont\xE9m os dados do Tilemap a serem exibidos. Isto geralmente \xE9 o arquivo JSON exportado do Tiled.","Set the Tilemap JSON file of _PARAM0_ to _PARAM1_":"Define o arquivo JSON do Tilemap de _PARAM0_ para _PARAM1_","The Tilemap object can be used to display tile-based objects. It's a good way to create maps for RPG, strategy games or create objects by assembling tiles, useful for platformer, retro-looking games, etc...":"O objeto Tilemap pode ser usado para exibir objetos baseados em blocos. \xC9 uma boa maneira de criar mapas para RPG, jogos de estrat\xE9gia ou criar objetos juntando blocos, \xFAteis para a plataforma, jogos retr\xF4, etc...","A text must start with a double quote (\").":"Um texto deve come\xE7ar com aspas (\").","A text must end with a double quote (\"). Add a double quote to terminate the text.":"Um texto deve terminar com aspas (\"). Adicione aspas para encerrar o texto.","A number was expected. You must enter a number here.":"Um n\xFAmero era esperado. Voc\xEA deve inserir um n\xFAmero aqui.","Smooth the image":"Suavizar a imagem","Always loaded in memory":"Sempre carregado na mem\xF3ria","Preload as sound":"Pr\xE9-carregar como som","Loads the fully decoded file into cache, so it can be played right away as Sound with no further delays.":"Carrega o arquivo totalmente decodificado no cache, ent\xE3o ele pode ser tocado diretamente como Som sem mais atrasos.","Preload as music":"Pr\xE9-carregar como m\xFAsica","Prepares the file for immediate streaming as Music (does not wait for complete download).":"Prepara o arquivo para streaming imediato como M\xFAsica (n\xE3o espera o download completo).","Preload in cache":"Pr\xE9-carregar em cache","Loads the complete file into cache, but does not decode it into memory until requested.":"Carrega o arquivo completo no cache, mas n\xE3o decodifica na mem\xF3ria at\xE9 ser solicitado.","Disable preloading at game startup":"Desativar pr\xE9-carregamento na inicializa\xE7\xE3o do jogo","You need to specify the name of the child variable to access. For example: `MyVariable.child`.":"You need to specify the name of the child variable to access. For example: `MyVariable.child`.","You need to specify the name of the child variable to access. For example: `MyVariable[0]`.":"You need to specify the name of the child variable to access. For example: `MyVariable[0]`.","An object variable or expression should be entered.":"An object variable or expression should be entered.","This variable does not exist on this object or group.":"This variable does not exist on this object or group.","This variable only exists on some objects of the group. It must be declared for all objects.":"Essa vari\xE1vel s\xF3 existe em alguns objetos do grupo. Ele deve ser declarado para todos os objetos.","This group is empty. Add an object to this group first.":"Esse grupo est\xE1 vazio. Adicione um objeto nesse grupo primeiro.","No child variable with this name found.":"No child variable with this name found.","Accessing a child variable of a property is not possible - just write the property name.":"Accessing a child variable of a property is not possible - just write the property name."